import {Box, Grid, Typography} from "@mui/material";
import Zoom from '@mui/material/Zoom';

import {styled} from '@mui/material/styles';
import Tooltip, {tooltipClasses} from '@mui/material/Tooltip';

function Skills() {

    const skills = ['Python', 'PyTorch', 'Tensorflow', 'React', 'Node.js', 'CSS', 'HTML', 'C++', 'OpenGL', 'C#', 'Unity']
    const grades = ['Proficient', 'Proficient', 'Competent', 'Competent', 'Competent', 'Competent', 'Competent', 'Competent', 'Novice', 'Novice', 'Novice']

    const SkillToolTop = styled(({ className, ...props }) => (
        <Tooltip {...props} classes={{ popper: className }} />
    ))(({ theme }) => ({
        [`& .${tooltipClasses.tooltip}`]: {
            backgroundColor: 'transparent',
            boxShadow: theme.shadows[1],
        },
        [`& .${tooltipClasses.arrow}`]: {
            color: theme.palette.text.primary,
        },
    }));


    const skillText = (skillName, skillGrade) => {
        let color = 'black';
        if (skillGrade === 'Novice') {
            color = '#0288d1'
        } else if (skillGrade === 'Competent') {
            color = '#388e3c'
        } else if (skillGrade === 'Proficient') {
            color = '#f57c00'
        } else if (skillGrade === 'Expert') {
            color = '#d32f2f'
        }
        return (
            <SkillToolTop
                disableFocusListener
                title={<Typography variant={'subtitle1'} fontWeight={'bolder'} color={color}>{skillGrade}</Typography>}
                placement="top"
                arrow
                sx={{maxWidth:'100%'}}
                TransitionComponent={Zoom}
            >
                <Typography variant={'h5'} fontWeight={'bolder'} color={'background.light'} display={'block'}>{skillName}</Typography>
            </SkillToolTop>
        );
    }

    const Skills = () => {
        return skills.map((skill, idx) => <Grid item xs={6} md={4} key={idx}>
            {skillText(skill, grades[idx])}
        </Grid>)

    }
    return (<Box alignItems={'center'} minHeight={window.innerHeight} marginX={'10%'}>
        <Box minHeight={150}/>
        <Typography variant={'h2'} fontWeight={'bolder'} marginBottom={12}>Skills</Typography>
        <Grid container spacing={12} justifyContent={'flex-between'}>
            <Skills/>
        </Grid>
    </Box>);
}

export default Skills;