import {Button, Grid, styled, Typography} from "@mui/material";
import IconButton from "@mui/material/IconButton";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import Brightness4Icon from "@mui/icons-material/Brightness4";

function Menu(props) {
    const colorMode = props.colorMode;
    const theme = props.theme;
    const setPage = props.setPage;

    const MenuButton = styled(Button)(({theme}) => ({
        color: theme.palette.text.primary, fontWeight: 'bolder', height: 60, '&:hover': {
            color: theme.palette.primary.dark, boxShadow: 'none'
        }, '&:active': {
            boxShadow: 'none', backgroundColor: 'none', color: theme.palette.primary.dark
        }, '&:focus': {
            color: theme.palette.primary.dark
        },
    }));

    const handleClick = (e) => {
        const key = e.currentTarget.id;
        setPage(key);
    }

    const menu = ["HOME", "CAREER", "SKILLS"];
    const MenuBar = () => {
        return menu.map((page) => <MenuButton key={page} id={[page.toLowerCase()]} onClick={handleClick} disableRipple
                                              disableElevation>{page}</MenuButton>);
    }

    return (<Grid container justifyContent="space-around" alignItems="center" height={100} position={'fixed'}
                  sx={{backdropFilter: "blur(3px)"}}>
        <Grid item xs={'auto'}>
            <Typography variant={'h5'} fontWeight={'bold'}>HideOnHouse</Typography>
        </Grid>
        <Grid item xs={'auto'}>
            <MenuBar/>
            <IconButton sx={{ml: 1}} onClick={colorMode.toggleColorMode} color="inherit">
                {theme.palette.mode === 'dark' ? <Brightness7Icon/> : <Brightness4Icon/>}
            </IconButton>
        </Grid>
    </Grid>);
}

export default Menu;
