import {Avatar, Stack, Typography} from "@mui/material";

import InfoCard from "./InfoCard";

function Home() {
    return (<Stack spacing={8} alignItems={'center'} height={window.innerHeight} display={'stretch'}>
            <Avatar
                sx={{width: 300, height: 300}}
                src="profile.jpg"
            />
            <Typography variant={'h5'} fontWeight={'bold'}>
                "   .    ."
            </Typography>
            <InfoCard/>
        </Stack>)
}

export default Home;