import {Box, Grid, Typography} from "@mui/material";

function InfoCard() {

    const InfoBox = (props) => {
        const subtitle = props.subtitle;
        const text = props.text;
        return (<Box>
                <Typography variant={'h5'} fontWeight={'bold'}>{subtitle}</Typography>
                <Typography variant={'h6'}>{text}</Typography>
            </Box>)
    }

    const infoTitle = ["", "", "", "", "", ""]
    const infoSub = ["", "1997.04.09", " ", "010-1234-5678", "fffuro@kookmin.ac.kr", " "]
    const Info = () => {
        return (infoTitle.map((text, index) => <Grid item xs={12} md={6} lg={4} key={index}>
                <InfoBox subtitle={text} text={infoSub[index]}/>
            </Grid>))
    }

    return (<Box>
            <Typography variant={'h3'} fontWeight={'bolder'}>About Me.</Typography>
            <Grid container p={4} spacing={2}>
                <Info/>
            </Grid>
        </Box>)
}

export default InfoCard;